.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.mainContainer {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 64px;
}

.dataContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 600px;
}

.header {
  text-align: center;
  font-size: 32px;
  font-weight: 600;
}

.bio {
  text-align: center;
  color: gray;
  margin-top: 16px;
}

.waveButton {
  cursor: pointer;
  margin-top: 16px;
  padding: 8px;
  border: 0;
  border-radius: 5px;
}

.totalWaves {
  text-align: center;
}

.messageBox {
  border: 2px solid orange;
  height: 3em;
  font-family: monospace;
  font-weight: 200;
  font-size: 18px;
}

.messageBox:focus {
  border: 2px solid blue;
  height: 3em;
  font-family: monospace;
  font-weight: 200;
  font-size: 18px;
}

.validMessage {
  border: 2px solid green;
  height: 3em;
  font-family: monospace;
  font-weight: 200;
  font-size: 18px;
}

.waveLogs {
  margin-top: 20px;
  text-align: center;
}
